define(["avalon", "text!./mc.pop.html", "./mc.pop.min.css"], function (avalon, template) {
    var _interface = function () { };
    avalon.component("mc:pop", {
        //#region 
        mcstyle: "", //
        disabled: false, //
        //#endregion

        //#region 
        onInit: _interface, //
        //#endregion

        //#region 
        isDisable: _interface, //
        showTip: _interface, //
        showSuccessTip: _interface, //
        showFailTip: _interface, //
        showConfirm: _interface, //
        //#endregion

        //#region 
        isInit: true,
        extend: {},
        lindex: -1, //
        trindex: -1, // [] - 
        tdindex: -1, // [] - 
        //#endregion

        //#region 

        //#endregion

        //#region 
        _trigger: _interface, //
        //#endregion

        $template: template,
        $replace: true,
        $construct: function (hooks, vmOpts, elemOpts) {
            var options = avalon.mix(hooks, vmOpts, elemOpts);
            return options;
        },
        $dispose: function (vm, elem) {
            elem.innerHTML = elem.textContent = "";
        },
        $init: function (vm, elem) {
            //#region 
            vm.isDisable = function () {
                return vm.disabled;
            }
            //#endregion

            //#region 

            vm.showTip = function (msg, time) {
                if (time === undefined) {
                    swal(msg);
                } else {
                    swal({ title: msg, timer: time });
                }
            }
            vm.showSuccessTip = function (msg, time) {
                if (time === undefined) {
                    swal({ title: msg, type: "success" });
                } else {
                    swal({ title: msg, type: "success", timer: time });
                }
            }
            vm.showFailTip = function (msg, time) {
                if (time === undefined) {
                    swal({ title: msg, type: "error" });
                } else {
                    swal({ title: msg, type: "error", timer: time });
                }
            }
            /**doc
             * 
             * @param {} title 
             * @param {} text 
             * @returns {Defferred} doneconfirm,failcancel
             */
            vm.showConfirm = function (title, text) {
                var def = $.Deferred();
                swal({
                    title: title,
                    text: text,
                    type: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#DD6B55",
                    confirmButtonText: "",
                    cancelButtonText: "",
                    closeOnConfirm: false, //bug confirm,todo loading?
                    closeOnCancel: true
                }, function (isConfirm) {
                    if (isConfirm) {
                        vm._trigger({}, "confirm");
                        def.resolve();
                    } else {
                        vm._trigger({}, "cancel");
                        def.reject();
                    }
                });
                return def;
            }
            //#endregion

            //#region 

            //#endregion

            //#region 
            vm._trigger = function (ev, type, _this) {
                switch (type) {
                    case "confirm":
                        if (typeof vm.onConfirm == "function") {
                            vm.onConfirm(ev, vm, _this);
                        }
                        break;
                    case "cancel":
                        if (typeof vm.onCancel == "function") {
                            vm.onCancel(ev, vm, _this);
                        }
                        break;
                    default: break;
                }
            }

            //#endregion
        },
        $ready: function (vm, elem) {
            //
            vm.onInit(vm, elem); //
            vm.isInit = false;
        }
    });

    var widget = avalon.components["mc:pop"];
    widget.regionals = {};
})


