define(["avalon", "text!./mc.datapager.html", "./mc.datapager.min.css"], function (avalon, template) {
    var _interface = function () { };
    avalon.component("mc:datapager", {
        //
        autoAjaxFirst: true, //
        active: false, //
        allCount: 0, //
        width: 100, //
        mcstyle: 0, //
        label: "", //
        curPage: 1,//
        jumpPageIndex: 1,//pageIndex
        pageSize: 0, //
        disabled: false, //

        $computed: {
            //
            allPageCount: {
                set: _interface,
                get: function () {
                    return Math.ceil(this.allCount / this.pageSize);
                }
            },
            //
            dataStartIndex: {
                set: _interface,
                get: function () {
                    return this.pageSize * (this.curPage - 1) + 1;
                }
            },
            //
            dataEndIndex: {
                set: _interface,
                get: function () {
                    //
                    if (this.curPage === this.allPageCount) {
                        return this.allCount;
                    } else {
                        return this.pageSize * this.curPage;
                    }
                }
            }
        },

        $page_size_opt: { //
            data: [
                {
                    text: "20", value: 20
                },
                {
                    text: "30", value: 30
                },
                {
                    text: "50", value: 50
                }
            ]
        },

        $ajaxTmpData: null, //ajax

        //ajax
        $ajaxConfig: {
            url: "",
            data: {},
            dataType: "json"
        },

        //
        onInit: _interface, //
        onClick: null, //tab
        onDataChange: null, //
        onPageChange: null, //

        //
        isDisable: _interface, //
        refreshPage: _interface, //
        addCondition: _interface, //(data),extend
        delCondition: _interface, //(data),key
        changeCondition: _interface, //(data),pageSizepageIndex

        //
        isInit: true,
        extend: {},
        $pageSelectVm: null, //vm
        $pageJumpVm: null, //iptvm
        $ajaxData: null, //ajax
        lindex: -1, //
        trindex: -1, // [] - 
        tdindex: -1, // [] - 

        //
        getSelectPageVM: _interface, //vm
        clickDrop: _interface, //
        getPageJumpVM: _interface, //iptvm
        changePageSize: _interface, //

        getAjaxData: _interface, //ajax
        setAjaxData: _interface, //ajax

        getAllCount: _interface, //allCount
        setAllCount: _interface, //allCount

        ajaxData: _interface, //
        turnPage: _interface, //
        nextPage: _interface, //
        prevPage: _interface, //
        jumpPage: _interface, //
        firstPage: _interface, //
        lastPage: _interface, //  

        //
        _trigger: _interface, //

        $template: template,
        //$replace: true,
        $construct: function (hooks, vmOpts, elemOpts) {
            vmOpts.$ajaxConfig && (vmOpts.$ajaxConfig = $.extend(hooks.$ajaxConfig, vmOpts.$ajaxConfig));
            var options = avalon.mix(hooks, vmOpts, elemOpts);
            return options;
        },
        $dispose: function (vm, elem) {
            elem.innerHTML = elem.textContent = "";
        },
        $init: function (vm, elem) {
            //var datapagerVM = avalon.define({
            //    $id: "datapager",
            //    $pageSelectVm: null,
            //    $page_size_opt: vm.$page_size_opt,
            //    getPageSelectVM: function(vm,elem) {
            //        datapagerVM.$pageSelectVm = vm;
            //    }
            //});
            //
            vm.clickDrop = function (ev) {
                if (!vm.disabled) {
                    vm.active = !vm.active;
                    vm._trigger(ev, "click");
                    ev.stopPropagation();
                }
            }
            vm.getSelectPageVM = function (_vm, elem) {
                vm.$pageSelectVm = _vm;
                vm.pageSize = _vm.getValue();
            }
            vm.getPageJumpVM = function (_vm, elem) {
                vm.$pageJumpVm = _vm;
            }
            vm.changePageSize = function (ev, _vm) {
                //bug 
                vm.pageSize = _vm.getValue();
            }
            //
            vm.jumpPage = function (ev) {
                var jumpPage = vm.$pageJumpVm.getValue() >> 0;
                if (jumpPage > 0 && jumpPage <= vm.allPageCount) {
                    vm._changeCurPage(ev, jumpPage);
                }
            }
            vm.nextPage = function (ev) {
                if (vm.curPage < vm.allPageCount) {
                    vm._changeCurPage(ev, vm.curPage + 1);
                }
            }
            vm.prevPage = function (ev) {
                if (vm.curPage > 1) {
                    vm._changeCurPage(ev, vm.curPage - 1);
                }
            }
            vm.firstPage = function (ev) {
                vm._changeCurPage(ev, 1);
            }
            vm.lastPage = function (ev, p) {
                vm._changeCurPage(ev, vm.allPageCount);
            }
            //ajax 
            vm.ajaxData = function () {
                var config = vm.$ajaxConfig;
                if (config.url !== "") {
                    var param = $.extend(config.data, { "pageIndex": vm.curPage, "pageSize": vm.pageSize });
                    Mc.Ajax({
                        url: config.url,
                        data: param,
                        success: function (response) {
                            vm.setAjaxData(response);
                            vm.$pageJumpVm.setValue(vm.curPage); //
                            vm._trigger({}, "data");
                        }
                    });
                }
            }
            vm.getAjaxData = function () {
                return vm.$ajaxData;
            }
            vm.setAjaxData = function (val) {
                vm.$ajaxData = val;
            }

            //allCount
            vm.getAllCount = function () {
                return vm.allCount;
            }
            //allCount
            vm.setAllCount = function (val) {
                var newAllCount = val >> 0;
                if (newAllCount > 0) {
                    vm.allCount = newAllCount;
                }
            }

            vm.refreshPage = function() {
                vm.ajaxData();
            }
            vm.addCondition = function (newData) {
                vm.$ajaxConfig.data = $.extend(vm.$ajaxConfig.data, newData);
            }
            vm.delCondition = function (keyArray) {
                $.each(keyArray, function (i, val) {
                    console.log(val);
                    delete vm.$ajaxConfig.data[val];
                });
            }
            vm.changeCondition = function (newData) {
                vm.$ajaxConfig.data = newData;
            }
            vm.isDisable = function () {
                return vm.disabled;
            }

            //#region 
            /**
             * 
             * @param {} ev 
             * @param {} type 
             * @returns {} 
             */
            vm._trigger = function (ev, type) {
                switch (type) {
                    case "data":
                        if (typeof vm.onDataChange == "function") {
                            vm.onDataChange(vm);
                        }
                        break;
                    case "size":
                        // if (typeof vm.onPageSizeChange == "function") {
                        //     vm.onPageSizeChange(ev, vm.pageSize);
                        //  }
                        break;
                    case "page":
                        if (typeof vm.onPageChange == "function") {
                            vm.onPageChange(ev, vm.curPage);
                        }
                        break;
                    default: break;
                }
            }
            //bug avalonwatch,...
            /**
             * 
             * @returns {} 
             */
            vm._changePageSize = function() {
                if (vm.curPage !== 1) {
                    vm.curPage = 1;
                }
                vm._changeCurPage({}, 1);
            }
            /**
             * 
             * @param {} ev 
             * @param {} p 
             * @returns {} 
             */
            vm._changeCurPage = function (ev, p) {
                if (vm.curPage !== p) {
                    vm.curPage = p;
                    vm.ajaxData();
                    vm._trigger(ev, "page");
                }
            }
            //#endregion

            ////,,ajax
            //vm.$watch("pageSize", function (newVal, oldVal) {
            //    vm.curPage = 1;
            //    //  vm._trigger({}, "size");
            //});
            ////
            //vm.$watch("curPage", function (newVal, oldVal) {
            //    vm.ajaxData();
            //    vm._trigger({ newVal: newVal, oldVal: oldVal }, "page");
            //});
        },
        $ready: function (vm, elem) {
            //
            vm.onInit(vm, elem); //
            if (vm.autoAjaxFirst) {
                vm.ajaxData(); //
            }
            vm.isInit = false;
        }
    });

    var widget = avalon.components["mc:datapager"];
    widget.regionals = {};
})


