function assignVM(vm, firstSource) {
    for (var i = 1; i < arguments.length; i++) {
        var nextSource = arguments[i];
        if (nextSource && typeof nextSource !== "object")
            continue;
        for (var j in vm) {
            if (vm.hasOwnProperty(j) && nextSource.hasOwnProperty(j)) {
                vm[j] = nextSource[j];
            }
        }
    }
    return vm;
}
