toSideBarData = function (data) {
    var result = [];
    //has checked end
    avalon.each(data, function (i, item) {
        result.push(item);
        item.select = item.select || false;
        item.children = item.children || [];
        // 
        if (item.children.length) {
            item.children = toSideBarData(item.children);
        }
    });
    return result;
};

define(['avalon', 'text!./mc.sidebar.html', './mc.sidebar.min.css'], function (avalon, template) {
    var _interface = function () { };
    avalon.component("mc:sidebar", {
        //
        active: 0, //
        padding: 0, //panel-body  padding
        //
        sidebarList: [],
        //
        isInit: true,
        extend: {},
        $allPanel: null,
        $allPanelBody: null,
        //view
        onInit: _interface, //
        changeData: _interface,
        renderContent: _interface,
        clickPanel: _interface,
        _trigger: _interface,
        setActive: _interface,
        getActive: _interface,
        setSelect: _interface,
        //
        $template: template,
        $replace: true,
        $construct: function (hooks, vmOpts, elemOpts) {
            if (vmOpts.data) {
                if (typeof (vmOpts.data) === 'function') {
                    vmOpts.data = vmOpts.data();
                }
                vmOpts.data = toSideBarData(vmOpts.data);
            }
            var options = avalon.mix(hooks, vmOpts, elemOpts);
            return options; //VM
        },
        $dispose: function (vm, elem) {
            elem.innerHTML = "";
        },
        $init: function (vm, elem) {
            //
            vm._trigger = function (ev, type) {
                switch (type) {
                    case 'changed':
                        if (typeof vm.onChanged == 'function') {
                            vm.onChanged(ev, {
                                "setActive": vm.setActive,
                                "getActive": vm.getActive
                            });
                        }
                        break;
                    default: break;
                }
            }
            //
            vm.renderContent = function (idx) {
                return vm['content' + idx];
            }
            vm.changeData = function (newData) {
                //trans
                vm.sidebarList = toSideBarData(newData);
            }
            vm.clickPanel = function (ev, idx) {
                avalon.log(idx);
                if (idx != vm.active) { //
                    //
                    vm.$allPanelBody.eq(vm.active).slideUp(300); //
                    vm.$allPanelBody.eq(idx).slideDown(300); //
                    vm.active = idx;
                } else {
                    //
                    vm.$allPanelBody.eq(idx).slideUp(300); //
                    vm.active = -1; //-1
                }
            }
            //
            vm.setActive = function (idx) {
                if (idx != vm.active) { //
                    //
                    vm.$allPanelBody.eq(vm.active).slideUp(300); //
                    vm.$allPanelBody.eq(idx).slideDown(300); //
                    vm.active = idx;
                }
            }
            vm.getActive = function () {
                return vm.active;
            }
            vm.setSelect = function (href) {
                //todo find
                vm.sidebarList.forEach(function (pat) {
                    //
                    if (pat.href) {
                        //href,
                        pat.select = false;
                        if (pat.href.toLowerCase().indexOf(href.toLowerCase()) > -1) {
                            pat.select = true;
                        }
                    }
                    //2..
                    //todo remove
                    _.each(pat.children, function (child) {
                        child.select = false;
                    });
                    //remove end
                    _.find(pat.children, function (child) {
                        if (child.href.toLowerCase().indexOf(href.toLowerCase()) > -1) {
                            child.select = true;
                            return false;
                        }
                    })
                })
            }
            
            //
            vm.$watch('sidebarList', function (newVal, oldVal) {
                setTimeout(function() {
                    vm.$allPanelBody = $(".mc-accordion-cotar .panel-body"); //$allPanelBody
                    //vm.$allPanelBody.eq(vm.active).slideDown(0); //
                }, 500);
            });

        },
        $ready: function (vm, elem) {
            //
            //vm.$allPanel = $(elem);
            vm.$allPanelBody = $(elem).find(".panel-body"); //$allPanelBody
            vm.$allPanelBody.eq(vm.active).slideDown(0); //
            vm.onInit(vm, elem); //
            vm.isInit = false;
            //if (typeof vm.panels[vm.active].fun == 'function') {
            //    vm.panels[vm.active].fun({}, vm);
            //}
        }
    });

    var widget = avalon.components["mc:sidebar"];
    widget.regionals = {};
});


