define(['avalon', 'text!./mc.letterlist.html', './mc.letterlist.min.css', 'mcButton'], function (avalon, template) {
    var _interface = function () { };
    avalon.component("mc:letterlist", {
        // 
        idName: "id",//id name
        textName: "text",// name
        keyName: "key", //key name  
        picName: "pic", //item name  
        hasPic:false,
        //
        onInit: _interface, //
        onDataChange: _interface, //
        onClickBefore: _interface, //
        // 
        setData: _interface, //
        getSelect: _interface, //
        //
        isInit: true,
        extend: {},
        lindex: -1, //   
        tmpActive: null,
        $originData: [],
        data: null,// [text,id,selected,key]
        q:"",
        $skipArray: [''],
        // 
        
        //
        _trigger: _interface, //  
        click: _interface, //  
        select : _interface, //  
        _select : _interface, //  
        genGroup: _interface, //  
        renderDataLen: _interface, //  
        $template: template,
        $replace: true,
        $construct: function (hooks, vmOpts, elemOpts) {
            if (vmOpts.data) {
                vmOpts.data = toLetterListData(vmOpts.data, vmOpts.cullOrigin);
            }
            var options = avalon.mix(hooks, vmOpts, elemOpts);
            return options; //VM 
        },
        $dispose: function (vm, elem) {
            elem.innerHTML = elem.textContent = "";
        },
        $init: function (vm, elem) {
            //


            //
            vm._trigger = function (ev, type) {
                switch (type) {
                    case "data":
                        if (typeof vm.onDataChange == "function") {
                            vm.onDataChange(vm.r, vm.originData, vm.diffData);
                        }
                        break;
                    default: break;
                }
            }
       
            //,radio
            vm.click = function (item) { 
                if (vm.onClickBefore(item,vm) === false) {
                    return false;
                }
                vm.select(item);
            }
            vm.select = function (item) {
                vm.tmpActive ? vm.tmpActive.selected = false : 0;
                vm._select(item, true);
                vm.tmpActive = item;
            }
            //
            vm._select = function (item, select) {
                item.selected = select; 
            }
            //q
            //todo filter = gen + watch,gen, | filter (var)
            var genGroup = function () { 
                var r = {};
                var q = vm.q.trim();
                //e.x{A:[ item ],B:[]  }..
                vm.$originData.forEach(function (item, i) {
                    if (q && item[vm.textName].indexOf(q) < 0) {
                        return false;
                    }
                    var key = item[vm.keyName];
                    r[key] = r[key] || [];
                    r[key].push(item);
                });
                vm.data = r;
            }
            vm.genGroup = _.throttle(genGroup, 250);
            vm.renderDataLen = function () {
                var data = vm.data.$model;
                var sum = 0;
                for (key in data) {
                    if (data[key] && data[key].length) {
                        sum += data[key].length;
                    }
                }


                return sum;
            }
            // 
            // 
            vm.setData = function (_data) {
                var data = toLetterListData(_data);
                vm.$originData = data;
                //vm.$originData = data.map(function (item) {
                //    return _.clone(item);
                //}) 
                vm.genGroup();
            }
            vm.getSelect = function () { 
                return  vm.tmpActive;
            }
            
            //
            //:ms-duplexms-inputwatch.. 
            vm.$watch('q', function (newV, oldV) {
                //selected 
                //avalon.each(vm.data, function (i, item) {
                //    item.selected = false;
                //});
                vm.genGroup();
            });
        },
        $ready: function (vm, elem) {
            //
            vm.onInit(vm, elem); //
            vm.isInit = false;
            vm.genGroup();
        }
    });

    var widget = avalon.components["mc:letterlist"];
    widget.regionals = {};
})


