
//todata
toListAddData = function (data, cullOrigin) {
    var r = [];
    avalon.each(data, function (i, item) {
        item.selected = false;
        item.checked = item.checked || false;
        if (cullOrigin) {
            if (!item.checked) {
                r.push(item);
            }
        } else {
            r.push(item);
        }

    });

    return r;
};
