define(['avalon', 'text!./mc.listDataAdd.html', './mc.listDataAdd.min.css', 'mcButton', 'underscore'], function (avalon, template) {
    var _interface = function () { };
    avalon.component("mc:listdataadd", {
        //
        data: [],// [text,id,checked,key] todo-->inside
        dataConfirmConfig: null,//
        ajax: false,//ajax
        idName: "id",//id name
        textName: "text",// name
        keyName: "key", //key name 
        cullOrigin: false,
        //
        onInit: _interface, //
        onDataChange: _interface, //
        // 
        setData: _interface, //
        //
        isInit: true,
        extend: {},
        lindex: -1, //
        allLen: { left: 0, right: 0 },// 
        addedList: [],// ,todo
        deledList: [],
        seletedLen: { left: 0, right: 0 }, //
        selectedAll: { left: false, right: false },
        originData: [],
        diffData: [],
        leftObj: {},//
        rightObj: {},//
        q: { left: "", right: "" },//
        r: null,//
        $skipArray: ['data', 'r', 'originData', 'diffData'],
        // 

        //
        _trigger: _interface, //
        genGroup: _interface, // 
        selectItem: _interface, //
        selectAll: _interface, //
        addItem: _interface, //  
        confirm: _interface, // 
        $template: template,
        $replace: true,
        $construct: function (hooks, vmOpts, elemOpts) {
            if (vmOpts.data) {
                vmOpts.data = toListAddData(vmOpts.data, vmOpts.cullOrigin);
            }
            var options = avalon.mix(hooks, vmOpts, elemOpts);
            return options; //VM 
        },
        $dispose: function (vm, elem) {
            elem.innerHTML = elem.textContent = "";
        },
        $init: function (vm, elem) {
            //


            //
            vm._trigger = function (ev, type) {
                switch (type) {
                    case "data":
                        if (typeof vm.onDataChange == "function") {
                            vm.onDataChange(vm.r, vm.originData, vm.diffData);
                        }
                        break;
                    default: break;
                }
            }
            var genGroup = function (str) {
                var str = 'left';
                var flag = false;

                //
                var r = {};
                vm.allLen[str] = vm.seletedLen[str] = 0;
                var foo = true;
                vm.data.forEach(function (item, i) {
                    // 
                    if (item.checked === flag) {
                        //q,,
                        var qStr = vm.q[str].trim();
                        if (qStr && item[vm.textName].indexOf(qStr) < 0) {
                            return false;
                        }
                        var key = item[vm.keyName];
                        r[key] = r[key] || [];
                        r[key].push(item);
                        //
                        //all
                        vm.allLen[str] += 1;
                        //selected
                        if (item.selected) {
                            vm.seletedLen[str] += 1;
                        } else {
                            foo = false;
                        }
                    }
                });
                if (foo && vm.allLen[str] > 0) {
                    vm.selectedAll[str] = true;
                } else {
                    vm.selectedAll[str] = false;
                }

                vm.leftObj = r;

                var str = 'right';
                var flag = true;
                //
                var r = {};
                var foo = true;
                vm.allLen[str] = vm.seletedLen[str] = 0;
                vm.data.forEach(function (item, i) {
                    // 
                    if (item.checked === flag) {
                        //q,,
                        var qStr = vm.q[str].trim();
                        if (qStr && item[vm.textName].indexOf(qStr) < 0) {
                            return false;
                        }
                        var key = item[vm.keyName];
                        r[key] = r[key] || [];
                        r[key].push(item);
                        //
                        //all
                        vm.allLen[str] += 1;
                        //selected
                        if (item.selected) {
                            vm.seletedLen[str] += 1;
                        } else {
                            foo = false;
                        }
                    }
                });
                if (foo && vm.allLen[str] > 0) {
                    vm.selectedAll[str] = true;
                } else {
                    vm.selectedAll[str] = false;
                }
                vm.rightObj = r;
            };
            vm.genGroup = _.throttle(genGroup, 200);


            vm.selectItem = function (id) {
                var r = vm.data.filter(function (item, i) {
                    return item[vm.idName] === id;
                });
                //
                if (r[0]) {
                    r[0].selected = !r[0].selected;
                    //
                    vm.genGroup();
                }
            }
            vm.selectAll = function (str) {
                var flag;
                if (str === 'left') {
                    flag = false;
                } else {
                    flag = true;
                }
                vm.selectedAll[str] = !vm.selectedAll[str];
                var selected = vm.selectedAll[str];
                avalon.each(vm.data, function (i, item) {
                    ///
                    if (item.checked === flag) {
                        item.selected = selected;
                    }
                })

                //
                vm.genGroup();

            }
            vm.addItem = function (flag, str) {
                //
                if (vm.seletedLen[str] < 1) return false;
                vm.data
                    .filter(function (item) {
                        return item.selected && (item.checked === flag)

                    })
                .forEach(function (item) {
                    item.selected = false;
                    item.checked = !flag;
                    //addedlist
                    //if (flag) {
                    //    //,
                    //    vm.addedList.remove();
                    //    vm.deledList.push();
                    //} else {
                    //    //,
                    //}
                });
                //
                vm.genGroup();
            }
            vm.confirm = function () {
                //ajax 
                var def = $.Deferred();
                if (vm.ajax) {
                    var config = vm.dataConfirmConfig;
                    var param = $.extend({}, config.data.$model);
                    $.ajax({
                        url: config.url,
                        type: "POST",
                        async: true,
                        data: param,
                        complete: function (result) {
                            console.log(result);
                            def.resolve(result);
                        }
                    });
                } else {

                    var r = [[], []];
                    avalon.each(vm.data, function (i, item) {
                        item.checked
                        ? r[1].push(item)
                        : r[0].push(item)
                    });
                    def.resolve(r);
                }
                //data,[ [],[] ]
                def.then(function (r) {
                    vm.r = r;
                    //diff
                    //diff[0],2diff 
                    var diff = [[], []];
                    var oriLeft = [];
                    avalon.each(vm.originData, function (i, item) {
                        item.checked
                        ? 0
                        : oriLeft.push(item);
                    });
                    var oriLeftPure = oriLeft.map(function (item) {
                        return item[vm.idName];
                    });
                    var rLeftPure = r[0].map(function (item) {
                        return item[vm.idName];
                    });
                    var tl = _.difference(oriLeftPure, rLeftPure);
                    var tr = _.difference(rLeftPure, oriLeftPure);
                    tl.forEach(function (t) {
                        var r = _.find(vm.originData, function (oriItem) {
                            return oriItem[vm.idName] === t
                        })
                        r ? diff[1].push(r)
                        : 0
                    })
                    tr.forEach(function (t) {
                        var r = _.find(vm.originData, function (oriItem) {
                            return oriItem[vm.idName] === t
                        })
                        r ? diff[0].push(r)
                        : 0
                    })
                    //
                    if (vm.cullOrigin) diff[0] = [];
                    vm.diffData = diff;
                    vm._trigger({}, 'data');
                })
            }
            // 
            // 
            vm.setData = function (data) {
                vm.originData = data.map(function (item) {
                    return _.clone(item);
                })
                vm.data = toListAddData(data, vm.cullOrigin);
                vm.genGroup();
            }

            //
            //:ms-duplexms-inputwatch..
            //todo ,.
            vm.$watch('q.*', function (newV, oldV) {
                //selected 
                avalon.each(vm.data, function (i, item) {
                    item.selected = false;
                });
                vm.genGroup();
            });
        },
        $ready: function (vm, elem) {
            //
            vm.onInit(vm, elem); //
            vm.isInit = false;
            //.. 
            vm.genGroup();
        }
    });

    var widget = avalon.components["mc:listdataadd"];
    widget.regionals = {};
})


