        $template: template,
        $replace: true,
        $construct: function (hooks, vmOpts, elemOpts) {
            if (vmOpts.tabConfig != undefined) {
                for (var i = 0; i < vmOpts.tabConfig.length; i++) {
                    hooks["content" + i] = "";
                }
            }
            var options = avalon.mix(hooks, vmOpts, elemOpts);
            return options;
        },
